<template>
  <div class="swiper-slide1">
    <div class="page">
      <music> </music>
      <div class="snow">
        <img
          src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1447/de66a4add15d14c6c657dc018595d056.gif"
        />
      </div>
      <div class="poster bounceIn animated"></div>
      <div class="item_wrapper">
        <!-- 圆形头像 -->
        <div class="avatar_wrapper">
          <div class="avatarlogo rotaterender">
            <img
              src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1434/e61aea3cfba8153a470afcb3cb67ee22.jpg?x-oss-process=image/resize,w_600"
            />
          </div>
        </div>
        <!-- 标签 -->
        <div class="weedLogo fadeInRight animated">
          <img
            src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1439/7d4ce9206d68b13b51c4631ffd60db1d.png?x-oss-process=image/resize,w_700"
          />
        </div>
        <div class="userNmae">吴先生 & 林小姐</div>
        <div class="render-img zoomIn">
          <img
            src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1444/846efb35611c705abaa3a134506da603.png?x-oss-process=image/resize,w_600"
          />
        </div>
        <div class="add-info">
          2020/5/20 13:14 星期日
          <br />合肥战鼓大酒店宴会厅
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import music from "../template/music"
export default {
  name: "page1",
  components:{music},
  data() {
    return {};
  }
};
</script>

<style scoped lang="scss">
@import "src/style/mixin";
@import "src/style/ani";
.swiper-slide1 {
  position: relative;
  width: 100%;
  height: 100%;
  .page {
    position: absolute;
    @include wh(100%, 100%);
    .poster {
      $poster: "https://res.zhangu365.com/fodder/designer/img/2019/0927/1434/f335d7cd5458cbd3874cd127ba7f374d.jpg?x-oss-process=image/resize,w_900";
      @include allcover();
      @include wh(100%, 100%);
      @include bis($poster);
      background-size: cover;
      background-position: center center;
      z-index: -1;
    }
    .item_wrapper {
      $item_wrapper: "https://res.zhangu365.com/fodder/designer/img/2019/0927/1439/84add1c2dcce93c523bce37c5a34f2a3.png";
      position: absolute;
      bottom: 0;
      left: 0;
      @include wh(100%, 12.4805rem);
      @include bis($item_wrapper);
      background-position: center 0;
      .avatar_wrapper {
        position: relative;
        @include wh(100%, 4.35rem);
        .avatarlogo {
          $avatarlogo: "https://res.zhangu365.com/fodder/designer/img/2019/0927/1436/b2edce9ec04b7d2ffe15b1bfb755e86a.png?x-oss-process=image/resize,w_300";
          @include wh(8.7305rem, 8.7305rem);
          @include bis($avatarlogo);
          position: relative;
          top: -5.96525rem;
          margin: 0 auto;
          background-position: 0 0;
          background-size: 8.701175rem;
          overflow: hidden;
          border-radius: 50%;
          transform-origin: center center;
          display: flex;
          justify-content: center;
          align-items: center;
          img {
            @include wh(6.628rem, 6.628rem);
            object-fit: cover;
            border-radius: 50%;
          }
        }
      }
      .weedLogo {
        @include pos(0.8935rem, -0.8065rem);
        @include wh(16.963rem, 5.361rem);
        img {
          @include wh(100%, 100%);
          object-fit: cover;
        }
        opacity: 0;
        animation: fadeInRight 0.6s ease 2.2s 1;
        animation-fill-mode: forwards;
      }
      .userNmae {
        text-align: center;
        width: 100%;
        @include pos(0, 4.55rem);
        @include font(1.25rem, 1.2);
        opacity: 0;
        animation: 0.6s ease 2.4s 1 zoomInLeft;
        animation-fill-mode: forwards;
      }
      .render-img {
        @include pos(1.875rem, 6.135rem);
        @include wh(15rem, 1.775rem);
        img {
          @include wh(100%, 100%);
          object-fit: cover;
        }
        opacity: 0;
        animation: zoomIn 0.6s ease 2.6s 1;
        animation-fill-mode: forwards;
      }
      .add-info {
        width: 100%;
        @include pos(0, 8.035rem);
        text-align: center;
        @include font(0.895rem, 1.4);
        opacity: 0;
        animation: bounceInRight 0.6s ease 2.6s 1;
        animation-fill-mode: forwards;
      }
    }
  }
  .snow {
    @include wh(89.68%, 0);
    margin: 0 auto;
    padding-bottom: 89.68%;
    img {
      width: 100%;
      object-fit: cover;
    }
  }
}

.rotaterender {
  animation: rotaterender 0.6s ease 2s 1;
  animation-fill-mode: forwards;
  -webkit-transform-origin: center;
  transform-origin: center;
  opacity: 0;
}
</style>


