<template>
  <div class="swiper-slide">
    <div class="top ">
      <music></music>
      <div class="bg">
        <img src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1434/58550379bfe2f1a4c5c019f91af53eea.jpg?x-oss-process=image/resize,w_800"/>
      </div>
      <div class="snow">
        <img src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1447/de66a4add15d14c6c657dc018595d056.gif"/>
      </div>
      <div class="star1 animated bounceIn">
        <img src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1439/74bee8f9c9c3d3b90f8bce4709e38d39.png?x-oss-process=image/resize,w_200"/>
      </div>
      <div class="star2 animated bounceIn">
          <img src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1439/74bee8f9c9c3d3b90f8bce4709e38d39.png?x-oss-process=image/resize,w_200"/>
      </div>
      <div class="shadowbg"></div>
      <div class="weedLogo animated lightSpeedIn">
          <img src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1439/7d4ce9206d68b13b51c4631ffd60db1d.png">
      </div>
    </div>
    <div class="card">
        <div class="title animated zoomInLeft">-诚挚邀请-</div>
        <div class="hrlogo animated zoomIn">
            <img src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1444/846efb35611c705abaa3a134506da603.png?x-oss-process=image/resize,w_600">
        </div>
        <div class="user animated bounceInRight">
            MR. 吴  <img src="~@/assets/stars.png" alt="" srcset="">    Miss. 林
        </div>
        <div class="info animated fadeInLeftBig">
            嗨！亲爱的朋友<br>我们结婚啦<br>谨定于2020年05月20日<br>举行结婚典礼<br>敬备喜宴 恭请光临<br>
        </div>
        <div class="botLogo animated bounceInRight">-WEDDING INVITATION-</div>
    </div>
  </div>
</template>

<script>
import music from "../template/music";
export default {
  name: "page2",
  data() {
    return {};
  },
  components: { music }
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";
@mixin images(){
    width: 100%;
    object-fit: cover;
}
.swiper-slide {
  position: relative;
  width: 100%;
  height: 100%;
  .top {
      position: relative;
    @include wh(100%, 16.675rem);
       animation: 0.6s ease 0s 1 normal both running bounceIn;
    .bg {
      position: relative;
      @include wh(100%, 100%);
      img {
        @include wh(100%, 100%);
        object-fit: cover;
      }
    }
    .snow {
      @include pos(1.15rem, 0);
      @include wh(16.45rem, 16.45rem);
      margin: 0 auto;
      img {
        width: 100%;
        object-fit: cover;
      }
    }
    .star1 {
     @include wh(3.3985rem,2.2315rem);
     @include pos(1.05rem,2.3rem);
     animation: 0.4s ease 2s infinite normal both running bounceIn;
     img{
         width: 100%;
         object-fit: cover;
        }
    }
    .star2 {
     @include wh(3.3985rem,2.2315rem);
     @include pos(14.05rem,5.3rem);
     animation: 0.4s ease 2s infinite normal both running bounceIn;
     img{
         width: 100%;
         object-fit: cover;
        }
    }
    .shadowbg{
        position: absolute;
        left: 0;
        bottom: 0;
        @include wh(100%,4.5115rem);
        background-image: linear-gradient(rgba(255,255,255,0),rgba(255,255,255,1));
    }
    .weedLogo{
     @include pos(0.8935rem,10.45rem);
     @include wh(16.963rem,5.3615rem);
     img{width:100%;object-fit: cover;}
     animation: 0.6s ease 0.4s 1 normal both running lightSpeedIn;
    }
  }
  .card{
      position: relative;
      .title{
          text-align: center;
          color: #333333;
          @include font(1.6rem,1.2);
          animation: 0.6s ease 0.4s 1 normal both running zoomInLeft;
      }
      .hrlogo{
          @include wh(15rem,1.775rem);
          img{width: 100%;object-fit: cover;}
          margin: 0 auto;
          animation: 0.6s ease 0.6s 1 normal both running zoomIn;
      }
      .user{
          display: flex;
          align-items: center;
          justify-content: center;
          text-align: center;
          color: #000;
          @include font(1.1rem,1.2);
          img{
              width: 1.5rem;
              height: 1.31rem;
              margin: 0 0.4rem;
          }
          animation: 0.6s ease 0.6s 1 normal both running bounceInRight;
      }
      .info{
          text-align: center;
           @include font(0.9rem,1.6);
           margin-top: 0.65rem;
           animation: 0.6s ease 0.8s 1 normal both running fadeInLeftBig;
      }
      .botLogo{
           text-align: center;
           @include font(1rem,1.6);
           margin-top: 0.35rem;
           letter-spacing:0.2rem;
           animation: 0.6s ease 0.6s 1 normal both running bounceInRight;
      }
  }
}
</style>


